/* ------------------------------------------------------------ *\
	Logo
\* ------------------------------------------------------------ */

.logo {
	font-family: $ff-gruppo;
	font-size: 38px;
	line-height: 1;
	text-decoration: none;
	transition: opacity $t-duration;

	/* Tablet Portrait */
	@media (max-width: 1023px) {
		font-size: 24px;
	}

	&:hover {
		opacity: 0.8;
	}
}
