/* ------------------------------------------------------------ *\
  widget
\* ------------------------------------------------------------ */

ul.widgets {
  list-style: none;
  margin-bottom: -20px;
}

.widget {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  .widget__image {
    flex: 0 0 64px;
    max-width: 64px;
    margin-right: 10px;

    img {
      margin: 0;
    }
  }

  .widget__content {
    flex: 1;
    max-width: 100%;
  }
}
