/* ------------------------------------------------------------ *\
	Main
\* ------------------------------------------------------------ */

.main {
	width: clamp(1024px, 70%, 1600px);
	margin: 0 auto;
	padding: 40px 15px;
	overflow: hidden;

	/* Mobile */
	@media (max-width: 1023px) {
		max-width: 100%;
	}

	.main__head {
		text-align: center;
	}

	.main__body {
		display: flex;
		margin: 0 -15px;

		/* Mobile */
		@media (max-width: 1023px) {
			flex-direction: column-reverse;
			margin: 0;
		}
	}

	.main__aside {
		flex: 0 0 33.33%;
		max-width: 33.33%;
		padding: 0 15px;

		/* Mobile */
		@media (max-width: 1023px) {
			flex: auto;
			max-width: 100%;
			padding: 0;
		}
	}

	.main__content {
		flex: 0 0 66.66%;
		max-width: 66.66%;
		padding: 0 15px;

		/* Mobile */
		@media (max-width: 1023px) {
			flex: auto;
			max-width: 100%;
			padding: 0;
		}
	}
}
