/* ------------------------------------------------------------ *\
  category
\* ------------------------------------------------------------ */

.categories {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 -15px -30px;

  .categories__item {
    flex: 0 0 150px;
    max-width: 150px;
    margin-bottom: 30px;
    padding: 0 15px;
  }
}

.category {
  text-align: center;

  a {
    color: $c-menu-link;
    text-decoration: none;
    transition: color $t-duration;
  }

  .category__image {
    position: relative;
    opacity: 0.85;
    transition: opacity $t-duration;

    a {
      display: block;
    }

    img {
      min-width: 80px;
      margin-bottom: 7px;
    }
  }

  &:hover .category__title a,
  &:hover .category__image {
    color: $c-white;
    opacity: 1;
  }
}
