/* ------------------------------------------------------------ *\
	Button
\* ------------------------------------------------------------ */
.btn {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  appearance: none;
}

/*  Btn block  */
.btn--block {
  display: block;
  padding-left: 0;
  padding-right: 0;
}

/* ------------------------------------------------------------ *\
  category
\* ------------------------------------------------------------ */
.categories {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 -15px -30px;
}
.categories .categories__item {
  flex: 0 0 150px;
  max-width: 150px;
  margin-bottom: 30px;
  padding: 0 15px;
}

.category {
  text-align: center;
}
.category a {
  color: #bbb;
  text-decoration: none;
  transition: color 0.3s;
}
.category .category__image {
  position: relative;
  opacity: 0.85;
  transition: opacity 0.3s;
}
.category .category__image a {
  display: block;
}
.category .category__image img {
  min-width: 80px;
  margin-bottom: 7px;
}
.category:hover .category__title a, .category:hover .category__image {
  color: #fff;
  opacity: 1;
}

