	Checkbox
\* ------------------------------------------------------------ */
/* ------------------------------------------------------------ *\
  container
\* ------------------------------------------------------------ */
.container {
  background-color: #242424;
  border: solid 1px #484848;
  opacity: 0.93;
  margin-bottom: 30px;
  padding: 20px;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  line-height: 1.6;
  transition: opacity 0.3s;
  /* Tablet-portrait */
}
@media (max-width: 1023px) {
  .container {
    opacity: 1;
    margin-bottom: 15px;
    padding: 10px;
  }
}
.container:hover {
  opacity: 1;
}
.container.always-visible {
  opacity: 1;
}

/* ------------------------------------------------------------ *\
	Fields
\* ------------------------------------------------------------ */
/*  Field textarea  */
/* ------------------------------------------------------------ *\
	Footer
\* ------------------------------------------------------------ */
/* ------------------------------------------------------------ *\
	Grid
\* ------------------------------------------------------------ */
.grid {
  display: flex;
  flex-flow: row wrap;
  /*  Grid col 1of2  */
  /*  Grid col 1of3  */
}
.grid .grid__col {
  max-width: 100%;
  flex: 1;
}
.grid .grid__col--1of2 {
  max-width: 50%;
  flex: 0 0 50%;
}
.grid .grid__col--1of3 {
  max-width: calc(100% / 3);
  flex: 0 0 calc(100% / 3);
}

/* ------------------------------------------------------------ *\
	Header
\* ------------------------------------------------------------ */
.header {
  color: #fff;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}
.header .header__inner {
  background-color: #242424;
  box-shadow: 0 0 10px #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 15px 30px;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .header .header__inner {
    padding: 10px 15px;
  }
}
.header .nav__open {
  transform: translateY(4px);
}

/* ------------------------------------------------------------ *\
	Icons
\* ------------------------------------------------------------ */
/* ------------------------------------------------------------ *\
  legend
\* ------------------------------------------------------------ */
.legend {
  border: solid 1px #484848;
  margin-bottom: 20px;
  padding: 10px;
}
.legend h4 {
  margin-bottom: 10px;
  font-size: 24px;
}
.legend span[class^=rarity-] {
  display: block;
}
.legend span[class^=rarity-]::before {
  content: "";
  width: 10px;
  height: 40px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
.legend span.rarity-1::before {
  background-color: limegreen;
}
.legend span.rarity-2::before {
  background-color: yellow;
}
.legend span.rarity-3::before {
  background-color: orange;
}
.legend span.rarity-4::before {
  background-color: red;
}

/* ------------------------------------------------------------ *\
	Logo
\* ------------------------------------------------------------ */
.logo {
  font-family: "Gruppo", cursive;
  font-size: 38px;
  line-height: 1;
  text-decoration: none;
  transition: opacity 0.3s;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .logo {
    font-size: 24px;
  }
}
.logo:hover {
  opacity: 0.8;
}

/* ------------------------------------------------------------ *\
	Main
\* ------------------------------------------------------------ */
.main {
  width: clamp(1024px, 70%, 1600px);
  margin: 0 auto;
  padding: 40px 15px;
  overflow: hidden;
  /* Mobile */
}
@media (max-width: 1023px) {
  .main {
    max-width: 100%;
  }
}
.main .main__head {
  text-align: center;
}
.main .main__body {
  display: flex;
  margin: 0 -15px;
  /* Mobile */
}
@media (max-width: 1023px) {
  .main .main__body {
    flex-direction: column-reverse;
    margin: 0;
  }
}
.main .main__aside {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  padding: 0 15px;
  /* Mobile */
}
@media (max-width: 1023px) {
  .main .main__aside {
    flex: auto;
    max-width: 100%;
    padding: 0;
  }
}
.main .main__content {
  flex: 0 0 66.66%;
  max-width: 66.66%;
  padding: 0 15px;
  /* Mobile */
}
@media (max-width: 1023px) {
  .main .main__content {
    flex: auto;
    max-width: 100%;
    padding: 0;
  }
}

/* ------------------------------------------------------------ *\
	Nav
\* ------------------------------------------------------------ */
.nav {
  padding: 0 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  letter-spacing: 0.02em;
  color: #bbb;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .nav {
    width: 90%;
    height: 100%;
    background-color: #484848;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
    transform: translateX(100%);
    padding: 25px 15px;
    transition: transform 0.3s;
  }
}
.nav.is-active {
  transform: none;
}
.nav ul {
  list-style: none;
  white-space: nowrap;
}
.nav ul.no-dropdown {
  display: flex;
  align-items: center;
  margin: 0 -20px;
  line-height: 1;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .nav ul.no-dropdown {
    display: block;
    margin: 0;
    line-height: 2.4;
    text-align: center;
  }
}
.nav ul.nav__level-1 {
  display: flex;
  align-items: center;
  margin: 0 -20px;
  line-height: 1;
}
.nav ul.nav__level-2 {
  background-color: #282828;
  border: solid 1px #484848;
  position: absolute;
  top: 100%;
  right: 0;
  transform: translateY(20px);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
}
.nav ul.nav__level-2::before {
  content: "";
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 100%;
  left: 0;
  display: block;
}
.nav ul.nav__level-2 li {
  padding: 10px 15px;
}
.nav li {
  position: relative;
  padding: 0 20px;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .nav li {
    color: #fff;
  }
}
.nav li:hover > a, .nav li:hover > span {
  color: #fff;
}
.nav li:hover > ul.nav__level-2 {
  opacity: 1;
  pointer-events: all;
}
.nav a,
.nav span {
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s;
}
.nav a:focus {
  color: #fff;
}
.nav .nav__close {
  width: 22px;
  height: 22px;
  position: absolute;
  top: 15px;
  right: 15px;
}
.nav .nav__close::before, .nav .nav__close::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #fff;
  border-radius: 10px;
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  transform-origin: center;
}
.nav .nav__close::before {
  transform: rotate(45deg);
}
.nav .nav__close::after {
  transform: rotate(-45deg);
}

/* ------------------------------------------------------------ *\
  note
\* ------------------------------------------------------------ */
.note {
  display: block;
  margin: -20px 0 20px;
}

/* ------------------------------------------------------------ *\
	Radio
\* ------------------------------------------------------------ */
/* ------------------------------------------------------------ *\
	Select
\* ------------------------------------------------------------ */
.select select::-ms-expand {
  display: none;
}

/* ------------------------------------------------------------ *\
  Shell
\* ------------------------------------------------------------ */
.shell {
  max-width: calc( 114rem + 1.5rem * 2 );
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: auto;
  width: 100%;
}

/* Shell Fluid */
.shell--fluid {
  max-width: none;
}

/* ------------------------------------------------------------ *\
  spoiler
\* ------------------------------------------------------------ */
.spoiler {
  padding: 10px 15px;
  border: solid 2px #000;
  border-radius: 6px;
  background: #111;
  position: relative;
}
.spoiler::after {
  content: "...";
  position: absolute;
  bottom: 5px;
  left: 15px;
  font-size: 30px;
  line-height: 0.9;
  letter-spacing: 0.02em;
}
.spoiler + .spoiler {
  margin-top: 15px;
}
.spoiler.is-active::after {
  content: "";
}
.spoiler.is-active .spoiler__title a::after {
  transform: translateY(-3px) rotate(180deg);
}
.spoiler a[href*=wowhead] {
  text-decoration: none;
}
.spoiler ul {
  padding-left: 20px;
}
.spoiler li > ul {
  margin-bottom: 10px;
}
.spoiler .spoiler__title {
  border-bottom: 1px solid #484848;
  cursor: pointer;
  margin-bottom: 10px;
}
.spoiler .spoiler__title a {
  display: block;
  padding-bottom: 5px;
  text-decoration: none;
}
.spoiler .spoiler__title a::after {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-width: 7px;
  border-top-color: currentColor;
  transform: translateY(4px);
  transform-origin: center;
  display: inline-block;
  margin-left: 5px;
}

/* ------------------------------------------------------------ *\
  table
\* ------------------------------------------------------------ */
.table {
  border: solid 1px #484848;
  margin-bottom: 20px;
  font-size: 14px;
  text-align: center;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .table {
    overflow: auto;
  }
}
.table th,
.table td {
  position: relative;
  padding: 5px 10px;
}
.table th {
  background-color: #000;
  font-weight: 300;
}
.table tr:nth-child(even) td {
  background-color: #282828;
}
.table tr:hover td {
  background-color: #484848;
}
.table td a {
  font-weight: 400;
  text-decoration: none;
  /* Tablet Portrait */
}
@media (max-width: 1023px) {
  .table td a {
    white-space: nowrap;
  }
}
.table td:first-child {
  text-align: left;
}
.table td.table__note {
  display: none;
}
.table tr.note-active td:not(:first-child):not(.table__note):not(:last-child) {
  display: none;
}
.table tr.note-active td.table__note {
  display: table-cell;
}
.table tr .table__note-trigger a span:last-child {
  display: none;
}
.table tr .table__note-trigger a span:first-child {
  display: block;
}
.table tr.note-active .table__note-trigger a span:last-child {
  display: block;
}
.table tr.note-active .table__note-trigger a span:first-child {
  display: none;
}
.table .table__note-trigger a {
  color: #484848;
  text-decoration: none;
  pointer-events: none;
}
.table .table__note-trigger.has-note a {
  font-weight: 700;
  color: #e6cc80;
  pointer-events: all;
}
.table .table__note {
  display: flex;
  justify-content: space-between;
}
.table tr.rarity-1 td:first-child::before {
  background-color: limegreen;
}
.table tr.rarity-2 td:first-child::before {
  background-color: yellow;
}
.table tr.rarity-3 td:first-child::before {
  background-color: orange;
}
.table tr.rarity-4 td:first-child::before {
  background-color: red;
}
.table tr[class^=rarity-] td:first-child::before {
  content: "";
  width: 10px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}

/* ------------------------------------------------------------ *\
  tabs
\* ------------------------------------------------------------ */
.tabs .tabs__nav ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.tabs .tabs__nav li {
  margin-right: 2px;
  margin-bottom: 2px;
}
.tabs .tabs__nav a {
  background-color: #484848;
  display: block;
  padding: 3px 25px;
  text-decoration: none;
}
.tabs .tabs__nav a:hover {
  background-color: #282828;
}
.tabs .tabs__nav a[aria-selected=true] {
  background-color: #000;
}
.tabs .tab {
  padding: 20px 0 0;
}

/* ------------------------------------------------------------ *\
  title-with-image
\* ------------------------------------------------------------ */
.title-with-image {
  display: flex;
  align-items: center;
}
.title-with-image img {
  margin-right: 10px;
}

/* ------------------------------------------------------------ *\
  widget
\* ------------------------------------------------------------ */
ul.widgets {
  list-style: none;
  margin-bottom: -20px;
}

.widget {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.widget .widget__image {
  flex: 0 0 64px;
  max-width: 64px;
  margin-right: 10px;
}
.widget .widget__image img {
  margin: 0;
}
.widget .widget__content {
  flex: 1;
  max-width: 100%;
}

/* ------------------------------------------------------------ *\
	Wrapper
\* ------------------------------------------------------------ */
.wrapper {
  position: relative;
  min-height: 100vh;
  background-image: url(../images/temp/background.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

/* ------------------------------------------------------------ *\
	Overwrites
\* ------------------------------------------------------------ */