/* ------------------------------------------------------------ *\
  tabs
\* ------------------------------------------------------------ */

.tabs {
  .tabs__nav {
    // margin-bottom: -1px;
  }

  .tabs__nav ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .tabs__nav li {
    margin-right: 2px;
    margin-bottom: 2px;
  }

  .tabs__nav a {
    background-color: $c-dark-3;
    display: block;
    padding: 3px 25px;
    text-decoration: none;

    &:hover {
      background-color: $c-dark-2;
    }

    &[aria-selected="true"] {
      background-color: $c-black;
    }
  }

  .tabs__body {
    // border: solid 1px $c-dark-3;
  }

  .tab {
    padding: 20px 0 0;
  }
}
