/* ------------------------------------------------------------ *\
  container
\* ------------------------------------------------------------ */

.container {
  background-color: $c-dark;
  border: solid 1px $c-dark-3;
  opacity: 0.93;
  margin-bottom: 30px;
  padding: 20px;
  color: $c-white;
  font-family: $ff-montserrat;
  font-weight: 300;
  line-height: 1.6;
  transition: opacity $t-duration;

  /* Tablet-portrait */
  @media (max-width: 1023px) {
    opacity: 1;
    margin-bottom: 15px;
    padding: 10px;
  }

  &:hover {
    opacity: 1;
  }

  &.always-visible {
    opacity: 1;
  }
}
